"use strict";

import React from "react";
import { RES_PATH } from "../../../sparkrc.js";
import { observer } from "mobx-react";
import store from "../../store/index";
import modalStore from "@src/store/modal";
import API from "../../api";
import {_throttle} from "@src/utils/utils"
import { hideLoading, showLoading, Toast } from '@spark/ui';
import "./gameFail.less";

@observer
class GameFail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      outTimer: 5,
    }
    this.timer = null
  }

  componentDidMount() {
    const {popData} = this.props;
    if(popData?.answerFlag) return false;
    const {outTimer} = this.state;
    let outTime = outTimer-1;
    this.timer = setInterval(() => {
      if(outTime>0) {
        this.setState({
          outTimer: outTime--
        })
      } else {
        this.clickOut();
      }
    },1000)
  }

  componentWillUnmount() {
    this.timer && clearTimeout(this.timer)
  }

  // 
  clickAgain = _throttle(async() => {
    const {popData} = this.props;
    if(popData?.answerFlag == 1) {
      this.startAnswer();
    } else {
      // 
      this.againPlay()
    }
  })

  // 
  againPlay = async() => {
    const {popData} = this.props;
    if (popData.reGold > store.homeInfo?.goldNum) {
      modalStore.pushPop("NoMoney");
      this.clickOut();
    } else {
      modalStore.closePop("GameFail");
      modalStore.pushPop("PayConfirm",{
        needCoin: popData?.reGold,
        start: this.confirmPay,
        miss: () => {
          modalStore.closePop("PayConfirm")
          this.clickOut();
        }
      })
    }
  }

  // 
  confirmPay = async() => {
    const {popData} = this.props;
    showLoading()
    const {success,data} = await API.resurgence({
      levelNum: store.currentGameLevel,
    })
    hideLoading()
    if(success&&data) {
      // Toast(`-${popData?.reGold}`)
      // ,
      store.setStartId(data);
      modalStore.closePop("PayConfirm")
      await popData.removeGame();
      popData.canvasUI();
    } else {
      modalStore.closePop("PayConfirm")
      this.clickOut();
    }
  }


  // 
  startAnswer = async () => {
    const {popData} = this.props;
    const { success, data } = await API.answerStart();
    if (success && data?.startId) {
      modalStore.closePop("GameFail")
      modalStore.pushPop("Answer",{
        startId: data.startId,
        removeGame: popData.removeGame,
        canvasUI:popData.canvasUI
      })
    }
  };

  clickOut = async() => {
    const {popData} = this.props;
    await popData.removeGame();
    modalStore.closePop("GameFail")
    store.changePage("Mappage")
  }
  
  render() {
    const {popData} = this.props;
    const {outTimer} = this.state;

    return (
      <div className="gameFailPanel">
        {
          popData?.answerFlag &&(
            <div className="inviteLimitbg"></div>
          )
        }
        <div className={popData?.answerFlag ?'content-gamefail2':'content-gamefail'}>
          <p className="title">{popData?.answerFlag ?'':``}</p>
          {popData?.answerFlag ? (
            <p className="subT">
              
              <br />
              
            </p>
          ) : (
            <p className="subT">{popData?.reGold || 0}</p>
          )}
        </div>
        {/*  */}
        <div className="again-fail" onClick={this.clickAgain}>
          <p className="goanswer">{popData?.answerFlag ?'':''}</p>
          <div className="participateInAnswer2">
            <p className="participationAnswer3">{popData?.answerFlag ? '' : `${store?.homeInfo?.goldNum || 0}`}</p>
          </div>
        </div>
        {/*  */}
        {
          popData?.answerFlag?(
            <p className="out" onClick={this.clickOut}></p>
          ):(
            <div className="out2" onClick={this.clickOut}>
              <div className="outt1"></div>
              <div className="outt2">{`${outTimer}s`}</div>
            </div>
          )
        }
      </div>
    );
  }
}
export default GameFail;
